<template>
  <div id="app" class="disable-select">
    <div class="top-panel">
      <a href="/">All Projects ></a>
    </div>
    <main>
      <div class="text-greeting">
        Welcome, <span class="greeting-name">{{ name }}</span>
      </div>
      <div class="tasks-div">
        <div class="text-section-title">
          <span>{{ taskEdit ? "Settings" : "Your tasks" }}</span>
        </div>
        <div class="tasks" v-if="!taskEdit">
          <div v-for="task in tasks" :key="task.title" class="task-item">
            <i
              v-if="task.done"
              class="fas fa-check-circle task-icon"
              @click="task.done = false"
            ></i>
            <i
              v-if="!task.done"
              class="far fa-circle task-icon"
              @click="task.done = true"
            ></i>
            <div class="task-text">{{ task.title }}</div>
          </div>
        </div>
        <div v-if="taskEdit">
          <div class="tasks-add-item">
            <div class="tasks-add-input">
              <input
                type="text"
                v-model="newTaskTitle"
                placeholder="Task title"
              />
            </div>
            <i class="fas fa-plus" @click="addTask()"></i>
          </div>
          <div class="tasks-edit-item">
            <i class="fas fa-user-tag"></i>
            <input
              type="text"
              placeholder="Your Name"
              v-model="name"
              @change="nameChange()"
            />
          </div>
          <!--<div class="text-settings-title">Add a task</div>-->
        </div>
      </div>
    </main>
    <i
      v-show="!taskEdit"
      class="fas fa-pen fab-edit"
      @click="taskEdit = !taskEdit"
    ></i>
    <i
      v-show="taskEdit"
      class="fas fa-times fab-edit"
      @click="taskEdit = !taskEdit"
    ></i>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      name: "Your Name",
      taskEdit: false,
      newTaskTitle: "",
      tasks: [],
    };
  },
  created() {
    if (localStorage.getItem("todo_tasks")) this.updateData();
    else localStorage.setItem("todo_tasks", "[]");
    if (localStorage.getItem("todo_name")) this.updateName();
    else localStorage.setItem("todo_name", this.name);
  },
  methods: {
    addTask() {
      if (this.newTaskTitle === "") return;
      let newTask = { title: this.newTaskTitle, done: false };
      this.tasks.push(newTask);
      console.log(newTask, this.tasks);
      this.uploadData();
      this.newTaskTitle = "";
    },
    uploadData() {
      localStorage.setItem("todo_tasks", JSON.stringify(this.tasks));
    },
    updateData() {
      this.tasks = JSON.parse(localStorage.getItem("todo_tasks"));
    },
    nameChange() {
      localStorage.setItem("todo_name", this.name);
    },
    updateName() {
      this.name = localStorage.getItem("todo_name");
    },
  },

  watch: {
    tasks: {
      handler: (newData) => {
        localStorage.setItem("todo_tasks", JSON.stringify(newData));
      },
      deep: true,
    },
  },
};
</script>

